.profile-editor {
  background-color: #fff;
  margin-top: 20px;
  box-sizing: border-box;
  padding: 30px 60px;
  margin-bottom: 20px;
  border: 1px solid #ebedf0;
}
.profile-editor h3 {
  font-size: 25px;
  font-weight: bold;
}
.profile-editor .profile-editor-form {
  display: flex;
  align-items: center;
}
.profile-editor .profile-editor-form .profile-form-field {
  width: 20%;
  font-size: 18px;
  color: #000;
}
.profile-editor .profile-editor-form .profile-form-val {
  flex: 1;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.profile-editor .profile-editor-form .profile-form-val > span {
  width: 300px;
}
.profile-editor .profile-editor-form .profile-form-val .profile-form-avatar-upload {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.profile-editor .profile-editor-form .profile-form-val .profile-form-avatar-upload > span {
  margin-bottom: 10px;
  font-size: 13px;
}
.profile-editor .profile-editor-form .profile-form-val .modify {
  color: #1890ff;
  cursor: pointer;
  margin-left: 100px;
}
